<template>
  <div class="clearfix">
    <div class="fromBox left">
      <!-- chegn-->
      <el-form :label-position="labelPosition" label-width="100px" :model="fromData">
        <el-form-item label="国家">
          <el-select v-model="fromData.companyName" placeholder="请选择">
            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="省份（州）">
          <el-select v-model="fromData.account" placeholder="请选择">
            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="城市">
          <el-select v-model="fromData.password" placeholder="请选择">
            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <div style="text-align: left">
            <el-button type="primary" round class="submit" v-on:click="submitForm('form')"> 选择 </el-button>
          </div>
        </el-form-item>
      </el-form>
      <!-- 表单-->
    </div>
    <div class="cityBox left">
      <div class="ctiye" :cityList="cityList">
        <div v-for="list in countryData" style="text-align: left">
          <div class="contyName">
            <span class="Country">
                          {{list.country.CN}}</span>
            <div class="cleae"></div>
          </div>
          <div class="cityList" >
            <div v-for="lis in list.country.province" style="font-size: 12px;line-height: 30px;">
              {{lis.CN?lis.CN+'：':''}}
              <span class="cityBoxText" v-for="li in lis.city" @click="clickCity(1,li,list.country.EN,list.country.CN,lis.EN,lis.CN,list.country.id)">
                          {{li.CN}}
                         </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
  .submit{ width: 130px;height: 34px;line-height: 0px;background-color: #0e72b9;margin-top:40px;}
  .submit:hover{background-color: #409eff }
   .cityBox{width:500px;height:295px;border: 1px solid #e1e1e1;overflow-y: scroll; margin-left:20px;background-color: white;padding:  20px; border-radius:8px; box-shadow:0 0 16px #e2e7ea; }
  .fromBox{width: 300px;border: 1px solid #e1e1e1;margin-left:20px;background-color: white;padding: 40px 20px 40px 20px; border-radius:8px; box-shadow:0 0 16px #e2e7ea;padding-bottom: 10px }
  .Country{font-weight: bold;}
  .cityList{ padding-left: 20px;}
  .contyName{padding-top: 20px;}
  .cityBoxText{padding: 5px 10px; cursor: pointer}
</style>
<script>
  import UserImg from '../../../static/img/icon/userDeFilt.png'
  import jsonListData from '../../../static/local.json'
  import {mapState, mapActions} from 'vuex'
  export default {
    name: 'my-cityList',
    data () {
      return {
        labelPosition: 'left',
        user: {
          imgUlr: UserImg, // 账号图片
          name: '小明管理员'
        },
        fromData: {
          companyName: null, // 商家名
          account: null, // 账号
          password: null // 密码
        },
        rules: {
          companyName: [
            { required: true, message: '请输入用户商家名', trigger: 'blur' },
            { min: 3, max: 20, message: '长度在 3 到 20 个字符', trigger: 'blur' }
          ],
          account: [
            { required: true, message: '请输入登陆账号', trigger: 'blur' },
            { min: 3, max: 20, message: '长度在 3 到 20 个字符', trigger: 'blur' }
          ],
          password: [
            { required: true, message: '请输入密码', trigger: 'blur' },
            { min: 3, max: 20, message: '长度在 3 到 20 个字符', trigger: 'blur' }
          ]
        },
        left: 'left',
        options: [{
          value: '选项1',
          label: '黄金糕'
        }, {
          value: '选项2',
          label: '双皮奶'
        }, {
          value: '选项3',
          label: '蚵仔煎'
        }, {
          value: '选项4',
          label: '龙须面'
        }, {
          value: '选项5',
          label: '北京烤鸭'
        }],
        countryData: [{
          country: {
            id: null,
            CN: null,
            EN: null,
            province: [{
              id: null,
              CN: null,
              EN: null,
              city: [{
                id: null,
                CN: null,
                EN: null
              }]
            }]
          }
        }]
      }
    },
    methods: {
      ...mapActions({
        addOderPost: 'addOderPost',
        getCityList: 'getCityList'
      }),
      tableRowClassName ({row, rowIndex}) {
        var x = rowIndex % 2
        if (x === 0) {
          return ''
        } else if (x === 1) {
          return 'success-row'
        }
        return ''
      },
      handleCurrentChange (val) {
        console.log(`当前页: ${val}`)
      },
      login () {
        this.$router.push({ name: "map" })
      },
      submitForm (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            console.log('提交')
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      // 城市列表展示
      cityListFn (res) {
        res = res.data
        var cityData = []
        console.log(res)
        for (let i in res) {
          // 拉取本地地址世界地址jsom
          var DATA = jsonListData
          // 申明单次循环地址
          var address = {
            country: {CN: '', id: '', EN: ''},
            province: {CN: '', id: '', EN: ''},
            city: {CN: '', id: '', EN: ''}
          }
          // 切割地址 分离国家城市
          var list = res[i].split('_')
          // 注入国家id
          address.country.id = list[0]
          address.country.CN = DATA[address.country.id].CN
          address.country.EN = DATA[address.country.id].EN
          if (list) {
            // 注入省份id
            address.province.id = address.country.id + '_' + list[1]
            address.province.CN = DATA[address.country.id][address.province.id].CN
            address.country.EN = DATA[address.country.id][address.province.id].EN
            // 注入城市id
            address.city.id = address.province.id + '_' + list[2]
            address.city.CN = DATA[address.country.id][address.province.id][address.city.id].CN
            address.city.EN = DATA[address.country.id][address.province.id][address.city.id].EN
          }
          cityData.push(address)
        }
        // 国家去重
        var result = [], result2 = [], hash = {};
        // 提出所有国家 和 重复的国家
        for (var i = 0; i < cityData.length; i++) {
          var elem = cityData[i].country.id
          if (!hash[elem]) {
            result.push({
              country: {
                id: cityData[i].country.id,
                CN: cityData[i].country.CN,
                EN: cityData[i].country.EN,
                province: [{
                  id: cityData[i].province.id,
                  CN: cityData[i].province.CN,
                  EN: cityData[i].province.EN,
                  city: [{
                    id: cityData[i].city.id,
                    CN: cityData[i].city.CN,
                    EN: cityData[i].city.EN
                  }]
                }]
              }
            })
            hash[elem] = true
          } else {
            result2.push({
              pid: cityData[i].country.id,
              id: cityData[i].province.id,
              CN: cityData[i].province.CN,
              EN: cityData[i].province.EN,
              city: [{
                id: cityData[i].city.id,
                CN: cityData[i].city.CN,
                EN: cityData[i].city.EN
              }]})
          }
        }
        // 组装 城市列表
        for (let i in result2) {
          for (let k in result) {
            var bol1 = true
            if (result2[i].pid == result[k].country.id){
              for (let s in result[k].country.province){
                if (result2[i].id == result[k].country.province[s].id){
                  var da = result[k].country.province[s].city
                  var da2 = result2[i].city
                  var dat3 = da.concat(da2)
                  bol1 = false
                  result[k].country.province[s].city = dat3
                }
              }
              if (bol1) {
                result[k].country.province.push(result2[i])
              }
            }
          }
        }
        this.countryData = result
      },
    },
    computed: {
      cityList: function () {
        var parm = this.cityListData
        if (parm) {
          this.cityListFn(parm.body)
        }
        return 1
      },
      ...mapState([
        'addOderData',
        'language',
        'cityListData'
      ])
    },
    mounted () {
      var par = {
        company: '大洋运输'
      }
      this.getCityList(par)
    },
    components: {
    }
  }
</script>




